<template>
  <div >
    <Index></Index>
    <h1 ></h1>

    <button @click="addUser" ></button>
    <AddUser :UserAlertType="UserAlertType" :UserModel="UserModel" @UserAlertNotice="UserAlertNotice"></AddUser>

    <table >
      <thead>
        <th width="50px"></th>
        <th width="80px"></th>
        <th width="100px"></th>
        <th width="200px"></th>
        <th width="150px"></th>
        <th width="250px"></th>
        <th width="100px"></th>
        <th width="100px"></th>
        <th width="50px"></th>
        <th width="100px"></th>
      </thead>
      <tbody>
        <tr v-for="(item,index) of UserModelList">
          <td>{{index|IndexAddOne}}</td>
          <td>{{item.name}}</td>
          <td>{{item.discription}}</td>
          <td >
            <!--<router-link :to="'/PosMachinel/'+item.id"> 1{{item.machineList}}</router-link>-->
            <router-link :to="'/PosMachinel/'+item.id">{{item.machineList|ListToMachineName}}</router-link>
          </td>
          <td>{{item.phone}}</td>
          <td>{{item.address}}</td>
          <td>{{item.remark}}</td>
          <td>{{item.addTime|DateFilter}}</td>
          <td>{{item.sort}}</td>
          <td>
            <button @click=updateUser(item)></button>
            <button @click=del(item)></button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import AddUser from '@/components/pos/AddUser';
  import Index from '@/components/pos/Index';
  import PosMachinel from '@/components/pos/PosMachinel';
  export default {
  data () {
    return {
      UserModelList:[],
      UserModel:{},
      UserAlertType:0//0 1 2
    }
  },
  methods:{
    UserInit(){
      this.$axios.get(this.baseUrl+'/user/findUser').then(req=>{
      //  console.log("req",req);
      this.UserModelList=req.data;
    })},
     addUser(){
        this.UserModel={};
        this.UserAlertType=1;
    },
      updateUser(e){
        this.UserModel={};
        this.UserAlertType=2;
        this.UserModel.id=e.id;
        this.UserModel.name=e.name;
        this.UserModel.remark=e.remark;
        this.UserModel.discription=e.discription
        this.UserModel.machineList=e.machineList;
        this.UserModel.phone=e.phone;
        this.UserModel.address=e.address;
        this.UserModel.sort=e.sort;
    },
    UserAlertNotice(e){
      console.log("",e)
      this.UserAlertType=0 //
      if(e=='sub'){
        this. UserInit();  //
      }
    },
    del(e){
      this.UserModel=e;
      console.log(this.UserModel);
      confirm("")
    }

  },
  mounted(){
    this.UserInit();
   },
    filters:{
      ListToMachineName(value){
      //  console.log("",value)
      //  console.log("value.length",value.length)

        if(value.length<1){
          return '';
        }
        var str='';
        for(var item of value){
          switch (item.type){
            case 1:
              str+='  '
                  break;
            case 2:
              str+='  '
                  break;
            case 3:
              str+='  '
                  break;
            default:
              str+='  '
                  break;
          }
        }

        return str;
      }
    },
  components:{AddUser,Index}


}
</script>

<style scoped>
#userMachine{
  border: solid 1px;
}

</style>



