<template>
  <div >

    <Index></Index>
    <h1></h1>
    <p >
      :<input type="date" v-model="consumeReq.startTime">
      :<input type="date" v-model="consumeReq.endTime">
      
      <select v-model="consumeReq.cardId">
        <option></option>
        <option></option>
      </select>
     :
      <select v-model="consumeReq.score">
        <option value="1"></option>
        <option value="0"></option>
      </select>
     :
      <select v-model="consumeReq.type">
        <option value="1"></option>
        <option value="2"></option>
        <option value="3"></option>
      </select>
      <button @click="clearParam"> </button>
      <button @click="findByParam"> </button>


    </p>
    <table>
      <thead>
      <tr>
        <th width="200px">()</th>
        <th width="150px"></th>
        <th width="100px"></th>
        <th width="100px"></th>
        <th width="100px"></th>
        <th width="100px"></th>
        <th width="200px"></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="intem of consumeList">
        <td>{{intem.createTime|DateFilter}}</td>
        <td>{{intem.creditcard.cardName}}</td>
        <td v-if="intem.type==1"></td>
        <td v-if="intem.type==2"></td>
        <td v-if="intem.score==1"></td>
        <td v-if="intem.score==0"></td>
        <td>{{intem.money}}</td>
        <td>{{intem.cost}}</td>
        <td>{{intem.remark}}</td>

      </tr>

      </tbody>
    </table>

  </div>
</template>

<script>
  import Index from '@/components/pos/Index';
export default {
  data(){
    return {
      consumeList:[],consumeReq:{}
    }
  },
  methods:{
    findByParam(){
      this.dataInit();
    },
    clearParam(){
      this.consumeReq={};
    },
    dataInit(){
      console.log(this.consumeReq)
      this.$axios.post(this.baseUrl+'/creditcard/consumefind',this.consumeReq).then(req=>{
        this.consumeList=req.data;
      //console.log(this.creditcardList)

    })}
  },
  mounted(){
    this.dataInit();
  },
  props: {
  }
    ,
  watch:{
  },
  components:{Index}

}
</script>

<style scoped>

</style>



